import {useState} from "react";
import {Loader} from "./index";
import {Avatar, Card, Col, Row, Select, Typography} from "antd";
import moment from "moment";

import {useGetCryptoNewsQuery} from "../services/cryptoNewsApi";
import {useGetCryptosQuery} from "../services/cryptoApi";


const {Title, Text} = Typography;
const {Option} = Select;

const News = ({simplified}) => {
	const [newsCategory, setNewsCategory] = useState("Cryptocurrency")
	const {data: cryptoNews} = useGetCryptoNewsQuery({newsCategory, count: simplified ? 6 : 12});
	const {data} = useGetCryptosQuery(100);


	const demoImageUrl = "https://www.maya.ph/hubfs/Maya/User%20Guide/Crypto/Crypto.png";

	if (!cryptoNews?.value) return <Loader/>;

	return (
		<Row gutter={[24, 24]}>
			{!simplified && (
				<Col span={24}>
					<Select
						showSearch
						className="select-news"
						placeholder="Select a Crypto"
						optionFilterProp="children"
						onChange={(value) => setNewsCategory(value)}
						filterOption={(input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
					>
						<Option value="Cryptocurrency">Cryptocurrency</Option>
						{data?.data?.coins.map((coin, i) => <Option key={i} value={coin.name}>{coin.name}</Option>)}
					</Select>
				</Col>
			)}
			{cryptoNews.value.map((news, i) => (
				<Col xs={24} sm={12} lg={8} key={i}>
					<Card hoverable className="news-card">
						<a href={news.url} target="_blank" rel="noreferrer">
							<div className="news-image-container">
								<Title className="news-title" level={4}>
									{news.name}
								</Title>
								<img
									style={{maxWidth: "180px", maxHeight: "100px"}}
									src={news?.image?.thumbnail?.contentUrl || demoImageUrl}
									alt="news_logo"
								/>
							</div>
							<p>{news.description > 100 ? `${news.description.substring(0, 100)}...` : news.description}</p>
							<div className="provider-container">
								<div>
									<Avatar
										src={news?.provider[0]?.image?.thumbnail?.contentUrl || demoImageUrl}
										alt="news"
										style={{marginRight: "5px"}}
									/>
									<Text className="provider-name">
										{news?.provider[0]?.name}
									</Text>
								</div>
								<Text>{moment(news.datePublished).startOf("ss").fromNow()}</Text>
							</div>
						</a>
					</Card>
				</Col>
			))}
		</Row>
	);
};

export default News;